/// <reference path="../../../../node_modules/monaco-editor/monaco.d.ts" />
import { AfterViewInit, Component, ElementRef, forwardRef, Input, OnDestroy, ViewChild } from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { Error } from 'cophi-lang';
import { BehaviorSubject, combineLatest, from, NEVER, Observable, Subject } from 'rxjs';
import { debounceTime, map, startWith, switchMap, take, takeUntil, tap } from 'rxjs/operators';
import { coherenceChecks, langData, LanguageDefinition, LanguagesService, ModelChangeEvent } from 'src/app/services/languages.service';
import { BaseComponentDirective } from '../golden-layout/base-component.directive';
import { EditorConfigService } from './editor-config.service';

let loadedMonaco = false;
let loadPromise: Promise<void>;

interface Markers {
  model: monaco.editor.ITextModel;
  lang: LanguageDefinition;
  errors: monaco.editor.IMarkerData[];
}

@Component({
  selector: 'app-monaco-editor',
  templateUrl: './monaco-editor.component.html',
  styleUrls: ['./monaco-editor.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => EditorConfigService),
      multi: true,
    }
  ]
})
export class MonacoEditorComponent extends BaseComponentDirective implements AfterViewInit, OnDestroy {

  @Input() title = 'Monaco Editor';
  @ViewChild('editorContainer', { static: true }) editorContainer!: ElementRef;
  editor!: monaco.editor.IStandaloneCodeEditor;
  options: monaco.editor.IStandaloneDiffEditorConstructionOptions = {};

  codeChange = new BehaviorSubject<string>('');
  @Input() set code(v: string) { this.codeChange.next(v); }
  get code() { return this.codeChange.value; }

  @Input() lang: string = 'gs-lit';

  private destroy = new Subject();

  private readonly ERROR_CHECK_DELAY = 1000; // the delay to wait after editor text has changed befoare updating errors

  editorModelChange = new Subject<ModelChangeEvent>();

  errors = this.editorModelChange.pipe(  // TODO simplify
    debounceTime(this.ERROR_CHECK_DELAY),
    switchMap(({ model, lang }) => (lang.errorProvider(model) as Promise<monaco.editor.IMarkerData[]>).then((errors) => ({ model, lang, errors }))),
    takeUntil(this.destroy),
  );

  checkChoerence = new Subject();

  warningsInput = new BehaviorSubject<Error[]>([]);

  warnings = this.warningsInput.pipe(
    map((x) => x.map(({ message, range: { start, end } }) => (
      {
        code: 'test',
        startLineNumber: start.line,
        startColumn: start.column,
        endLineNumber: end.line,
        endColumn: end.column,
        message,
        severity: monaco.MarkerSeverity.Warning,
      } as monaco.editor.IMarkerData
    ))),
    startWith<monaco.editor.IMarkerData[]>([]),
    takeUntil(this.destroy),
  ); // TODO

  errorsInfo = combineLatest([this.warnings, this.errors]).pipe(
    map(([warns, { errors, lang, model }]) => ({ lang, model, errors: [...errors, ...warns] })),
  )

  constructor(
    private el: ElementRef,
    private langService: LanguagesService,
  ) {
    super(el.nativeElement);
    this.editorModelChange.pipe(takeUntil(this.destroy)).subscribe((m) => this.code = m.model.getValue());

    this.errorsInfo.subscribe(({ errors }) => { // TODO: simplify
      const model = this.editor?.getModel();
      if (!!model) {
        monaco.editor.setModelMarkers(model, this.lang, errors)
      }
    });

    this.warnings.subscribe((warns) => {
      const model = this.editor?.getModel();
      if (!!model) {
        monaco.editor.setModelMarkers(model, this.lang, warns);
      }
    }); //TODO: remove me
  }

  ngOnChanges() { // TODO: check if still needed
    if (this.editor) {
      this.editor.setValue(this.code);
    }
  }

  ngAfterViewInit(): void {
    if (loadedMonaco) {
      // Wait until monaco editor is available
      loadPromise.then(() => {
        this.initMonaco();
      });
    } else {
      loadedMonaco = true;
      loadPromise = new Promise<void>((resolve: any) => {
        if (typeof ((<any>window).monaco) === 'object') {
          resolve();
          return;
        }
        const onAmdLoader: any = () => {
          // Load monaco
          (<any>window).require.config({ paths: { 'vs': 'assets/monaco/min/vs' } });

          (<any>window).require(['vs/editor/editor.main'], () => {
            this.initMonaco();
            resolve();
          });
        };

        // Load AMD loader if necessary
        if (!(<any>window).require) {
          const loaderScript: HTMLScriptElement = document.createElement('script');
          loaderScript.type = 'text/javascript';
          loaderScript.src = 'assets/monaco/min/vs/loader.js';
          loaderScript.addEventListener('load', onAmdLoader);
          document.body.appendChild(loaderScript);
        } else {
          onAmdLoader();
        }
      });
    }
  }

  ngOnDestroy(): void {
    this.destroy.next();
    this.destroy.complete();

    // if (this.editor !== undefined) {
    //   this.editor.dispose();
    // }
  }

  initMonaco(options: monaco.editor.IStandaloneDiffEditorConstructionOptions = {}) {
    console.log("LANG", this.lang, "LL", langData[this.lang])
    this.langService.registerLanguage(langData[this.lang], this.editorModelChange);
    this.editor = monaco.editor.create(this.el.nativeElement, {
      value: this.code,
      language: this.lang,
      theme: 'vs-dark',
      automaticLayout: true,
      fontFamily: 'IFAOGrec',
      lineNumbers: 'off',
      minimap: { enabled: false },
    });
  }

}

