import { AfterViewInit, Component, ElementRef, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import * as bootstrap from 'bootstrap';
import { BehaviorSubject, combineLatest, forkJoin, NEVER, Observable } from 'rxjs';
import { map, switchMap, tap } from 'rxjs/operators';

export interface TreeViewItem {
  id: string,
  label: string;
  children: string[];
  link?: string;
}

const EMPTY_TREE_VIEW_ITEM: TreeViewItem = { id: '', label: '', children: [] };

export interface TreeViewProvider {
  getItem: (id: string) => Observable<TreeViewItem>;
}

@Component({
  selector: 'app-tree-view',
  templateUrl: './tree-view.component.html',
  styleUrls: ['./tree-view.component.scss']
})
export class TreeViewComponent implements AfterViewInit {
  @ViewChild('childrenContainer', { static: true }) editorContainer!: ElementRef;
  childrenCollapsible!: bootstrap.Collapse;
  opened = false;

  @Output() itemClicked = new EventEmitter<string>()
  
  private _model: TreeViewItem = EMPTY_TREE_VIEW_ITEM;
  @Input() set model(v: TreeViewItem) {
    this._model = v;
    this.modelChange.next(this._model);

  }
  get model() { return this._model; }
  modelChange = new BehaviorSubject<TreeViewItem>(this._model);


  private _dataProvider: TreeViewProvider = { getItem: (_) => NEVER };
  dataProviderChange = new BehaviorSubject<TreeViewProvider>(this._dataProvider);
  @Input() set dataProvider(v: TreeViewProvider) {
    this._dataProvider = v
    this.dataProviderChange.next(this._dataProvider);

  }
  get dataProvider() { return this._dataProvider; }

  childrenLoaded = new BehaviorSubject<boolean>(false);

  children =
    combineLatest([
      this.dataProviderChange,
      this.modelChange,
    ]).pipe(
      map(([{ getItem }, { children }]) => children.map(getItem)),
      switchMap((x) => forkJoin(x)),
      tap(() => this.childrenLoaded.next(true)),
    )
    ;

  ngAfterViewInit() {
    this.childrenCollapsible = new bootstrap.Collapse(this.editorContainer.nativeElement, { toggle: false });
    this.childrenCollapsible.hide();

  }

  toggle() {
    if (!!this.model && this.model.children.length > 0) {
      this.childrenCollapsible.toggle();
      this.opened = !this.opened;
    }
  }

}
