import { ApplicationRef, Component, ComponentRef, ElementRef, EmbeddedViewRef, EventEmitter, Input, OnDestroy, Output, ViewChild, ViewContainerRef } from '@angular/core';
import {
  ComponentContainer, GoldenLayout,
  LayoutConfig,
  LogicalZIndex,
  ResolvedComponentItemConfig
} from "golden-layout";
import { extend } from 'openseadragon';
import { Subject } from 'rxjs';
import { takeUntil, tap } from 'rxjs/operators';
import { ComponentInfo } from 'src/app/services/mock-data.service';
import { EditorComponent } from '../../editor/editor.component';
import { MonacoEditorComponent } from '../../monaco-editor/monaco-editor.component';
import { OsdComponent } from '../../osd/osd.component';
import { TestComponentComponent } from '../../test-component/test-component.component';
import { BaseComponentDirective } from '../base-component.directive';
import { GoldenLayoutService } from '../golden-layout.service';

@Component({
  selector: 'app-golden-layout-host',
  template: '<ng-template #componentViewContainer></ng-template>',
  styleUrls: ['./golden-layout-host.component.scss'],
})
export class GoldenLayoutHostComponent implements OnDestroy {
  @Output() ready = new EventEmitter<GoldenLayoutHostComponent>();

  private _goldenLayout!: GoldenLayout;
  private _goldenLayoutElement: HTMLElement;
  private _componentRefMap = new Map<ComponentContainer, ComponentRef<BaseComponentDirective>>();
  private _goldenLayoutBoundingClientRect: DOMRect = new DOMRect();

  private _goldenLayoutBindComponentEventListener =
    (container: ComponentContainer, itemConfig: ResolvedComponentItemConfig) => this.handleBindComponentEvent(container, itemConfig);
  private _goldenLayoutUnbindComponentEventListener =
    (container: ComponentContainer) => this.handleUnbindComponentEvent(container);

  @ViewChild('componentViewContainer', { read: ViewContainerRef, static: true })
  private _componentViewContainerRef!: ViewContainerRef;

  get goldenLayout() { return this._goldenLayout; }
  get isGoldenLayoutSubWindow() { return this._goldenLayout.isSubWindow; }

  private _windowResizeListener = () => this.handleWindowResizeEvent();

  private destroy = new Subject();

  public saveLayout$ = new Subject();
  public loadLayout$ = new Subject();
  public addComponent$ = new Subject<[string, any]>() // component name, params

  private saveLayoutCmd = this.saveLayout$.pipe(
    tap(() => this.saveLayout()),
    takeUntil(this.destroy),
  );

  private loadLayoutCmd = this.loadLayout$.pipe(
    tap(() => this.loadLayout()),
    takeUntil(this.destroy),
  );

  private addComponentCmd = this.addComponent$.pipe(
    tap((x) => console.log(x)),
    tap(([name, params]) => this.add(name, params)),
    takeUntil(this.destroy),
  );

  constructor(private _appRef: ApplicationRef,
    private _elRef: ElementRef<HTMLElement>,
    private goldenLayoutComponentService: GoldenLayoutService,
  ) {
    this._goldenLayoutElement = this._elRef.nativeElement;

    this.goldenLayoutComponentService.registerComponentType(OsdComponent.name, OsdComponent);
    this.goldenLayoutComponentService.registerComponentType(TestComponentComponent.name, TestComponentComponent);
    this.goldenLayoutComponentService.registerComponentType(MonacoEditorComponent.name, MonacoEditorComponent);
    this.goldenLayoutComponentService.registerComponentType(EditorComponent.name, EditorComponent);

    this.saveLayoutCmd.subscribe();
    this.loadLayoutCmd.subscribe();
    this.addComponentCmd.subscribe((x) => console.log('add', x));

  }

  ngAfterViewInit() {
    setTimeout(() => { // TODO: try to remove settimeout
      this.initialise();

      globalThis.addEventListener('resize', this._windowResizeListener);

      this.add(MonacoEditorComponent.name, { lang: 'gs-lit', title: 'GSLIT', code: 'LIT' });

      this.add(MonacoEditorComponent.name, { lang: 'CustomLang', title: 'My Title 1', code: 'Custom 1' });
      this.add(MonacoEditorComponent.name, { lang: 'ppp', title: 'My Title 2', code: 'PPP 1' });
      // this.add(MonacoEditorComponent.name, { lang: 'CustomLang', title: 'My Title 3', code: 'Custom 2' });
      // this.add(MonacoEditorComponent.name, { lang: 'CustomLang', title: 'My Title 4', code: 'Custom 3' });

      setTimeout(() => this.resizeGoldenLayout(), 3000);
      this._goldenLayout.emit('resize');
      this.ready.next(this);
    }, 0);

  }

  private saveLayout() {
    const rlc = this._goldenLayout.saveLayout();
    const lc = LayoutConfig.fromResolved(rlc);
    localStorage.setItem('glc', JSON.stringify(lc))
  }

  private loadLayout() {
    const lc: LayoutConfig = JSON.parse(localStorage.getItem('glc') || '');
    this._goldenLayout.loadLayout(lc);
  }

  ngOnDestroy() {
    globalThis.removeEventListener('resize', this._windowResizeListener);
    this._goldenLayout.destroy();
  }

  private handleWindowResizeEvent() { this.resizeGoldenLayout(); }

  resizeGoldenLayout() {
    const { offsetWidth, offsetHeight } = this._elRef.nativeElement.parentElement ?? { offsetHeight: 0, offsetWidth: 0 };
    this.setSize(offsetWidth, offsetHeight);
  }

  clear() {
    this._goldenLayout.clear();
  }

  add(componentName: string, params: { title: string } & any): ComponentRef<BaseComponentDirective> {
    const goldenLayoutComponent = this._goldenLayout.newComponent(componentName, undefined, params.title); // do not set state here
    const componentRef = this.getComponentRef(goldenLayoutComponent.container);
    if (componentRef === undefined) {
      throw new Error('Unexpected error getting ComponentRef');
    }

    if (componentName === MonacoEditorComponent.name) { // TODO refactor this list of ifs
      const c = componentRef.instance as MonacoEditorComponent;
      c.lang = params.lang;
      c.title = params.title;
      c.code = params.code;
    }

    if (componentName === OsdComponent.name) {
      const c = componentRef.instance as OsdComponent;
      c.manifestURL = params.manifestURL; //'https://www.e-codices.unifr.ch/metadata/iiif/ubb-M-I-0018/manifest.json';
    }

    this.resizeGoldenLayout();
    return componentRef;
  }

  initialise() {
    this._goldenLayout = new GoldenLayout(
      this._goldenLayoutElement,
      this._goldenLayoutBindComponentEventListener,
      this._goldenLayoutUnbindComponentEventListener,
    );
    this._goldenLayout.beforeVirtualRectingEvent = (count) => this.handleBeforeVirtualRectingEvent(count);

    if (this._goldenLayout.isSubWindow) {
      this._goldenLayout.checkAddDefaultPopinButton();
    }
  }

  setSize(width: number, height: number) {
    this._goldenLayout.setSize(width, height)
  }

  getComponentRef(container: ComponentContainer) {
    return this._componentRefMap.get(container);
  }

  private handleBindComponentEvent(container: ComponentContainer, itemConfig: ResolvedComponentItemConfig): ComponentContainer.BindableComponent {
    const componentType = itemConfig.componentType;
    const componentRef = this.goldenLayoutComponentService.createComponent(componentType, container);
    const component = componentRef.instance;

    // TODO: manage state recovery after moving component
    // const c = componentRef.instance as OsdComponent;
    // c.manifestURL = 'https://www.e-codices.unifr.ch/metadata/iiif/bge-gr0044/manifest.json'

    this._componentRefMap.set(container, componentRef);

    container.virtualRectingRequiredEvent = (container, width, height) => this.handleContainerVirtualRectingRequiredEvent(container, width, height);
    container.virtualVisibilityChangeRequiredEvent = (container, visible) => this.handleContainerVisibilityChangeRequiredEvent(container, visible);
    container.virtualZIndexChangeRequiredEvent = (container, logicalZIndex, defaultZIndex) => this.handleContainerVirtualZIndexChangeRequiredEvent(container, logicalZIndex, defaultZIndex);

    this._appRef.attachView(componentRef.hostView);
    const componentRootElement = component.rootHtmlElement;
    this._goldenLayoutElement.appendChild(componentRootElement);


    return {
      component,
      virtual: true,
    }
  }

  private handleUnbindComponentEvent(container: ComponentContainer) {
    const componentRef = this._componentRefMap.get(container);
    if (componentRef === undefined) {
      throw new Error('Could not unbind component. Container not found');
    }
    this._componentRefMap.delete(container);

    const hostView = componentRef.hostView;

    const component = componentRef.instance;
    const componentRootElement = component.rootHtmlElement;
    this._goldenLayoutElement.removeChild(componentRootElement);
    this._appRef.detachView(hostView);

    componentRef.destroy();
  }

  private handleBeforeVirtualRectingEvent(count: number) {
    this._goldenLayoutBoundingClientRect = this._goldenLayoutElement.getBoundingClientRect();
  }

  private handleContainerVirtualRectingRequiredEvent(container: ComponentContainer, width: number, height: number) {
    const containerBoundingClientRect = container.element.getBoundingClientRect();
    const left = containerBoundingClientRect.left - this._goldenLayoutBoundingClientRect.left;
    const top = containerBoundingClientRect.top - this._goldenLayoutBoundingClientRect.top;

    const componentRef = this._componentRefMap.get(container);
    if (componentRef === undefined) {
      throw new Error('handleContainerVirtualRectingRequiredEvent: ComponentRef not found');
    }
    const component = componentRef.instance;
    component.setPositionAndSize(left, top, width, height);
  }

  private handleContainerVisibilityChangeRequiredEvent(container: ComponentContainer, visible: boolean) {
    const componentRef = this._componentRefMap.get(container);
    if (componentRef === undefined) {
      throw new Error('handleContainerVisibilityChangeRequiredEvent: ComponentRef not found');
    }
    const component = componentRef.instance;
    component.setVisibility(visible);
  }

  private handleContainerVirtualZIndexChangeRequiredEvent(container: ComponentContainer, logicalZIndex: LogicalZIndex, defaultZIndex: string) {
    const componentRef = this._componentRefMap.get(container);
    if (componentRef === undefined) {
      throw new Error('handleContainerVirtualZIndexChangeRequiredEvent: ComponentRef not found');
    }
    const component = componentRef.instance;
    component.setZIndex(defaultZIndex);
  }
}
