import { Component } from '@angular/core';
import { NEVER, Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';
import { TreeViewItem, TreeViewProvider } from './components/tree-view/tree-view.component';
import { MockDataService } from './services/mock-data.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'cophi-ui';

  treeItems: Observable<TreeViewItem[]>;
  treeProvider: TreeViewProvider;

  constructor(
    private mockDB: MockDataService,
  ) {

    this.treeItems = of(this.mockDB.rootElements()).pipe(
      map((x) => x.map(({ id, label, children }) => ({ id, label, children } as TreeViewItem))),
    );

    this.treeProvider = {
      getItem: (id: string) => {
        const di = this.mockDB.item(id);
        if (di === undefined) { return NEVER; }
        return of({ label: di.label, children: di.children } as TreeViewItem);
      }
    }
  }
}
