import { AfterViewInit, Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import * as bootstrap from 'bootstrap';
import { NEVER, never, Observable, Subject, Subscription } from 'rxjs';
import { tap } from 'rxjs/operators';

@Component({
  selector: 'app-offcanvas',
  templateUrl: './offcanvas.component.html',
  styleUrls: ['./offcanvas.component.scss']
})
export class OffcanvasComponent implements AfterViewInit {

  @ViewChild('offcanvas', { static: true }) offcanvasElement!: ElementRef;
  offcanvas!: bootstrap.Offcanvas;

  @Input() header: string | undefined
  @Input() position: 'start' | 'end' | 'top' | 'bottom' = 'start'
  private _openEvents: Observable<any> = NEVER;
  private s: Subscription = new Subscription();
  @Input() set openEvents(v: Observable<any>) {
    this._openEvents = v;
    console.log("new open events!", v);
    this.s.unsubscribe();
    this.s = this._openEvents.pipe(
      tap(() => this.offcanvas.toggle()),
      tap(() =>  console.log("HERE ")),
    ).subscribe();
  }
  get openEvents() { return this._openEvents; } 

  ngAfterViewInit(): void {
    this.offcanvas = new bootstrap.Offcanvas(this.offcanvasElement.nativeElement);
  }
}
