import { AfterViewInit, Component, ElementRef, EventEmitter, Inject, Input, NgZone, OnDestroy, Output, ViewChild } from '@angular/core';

import { HttpClient } from '@angular/common/http';

import { BehaviorSubject, combineLatest, Observable, Subscription } from 'rxjs';
import { distinctUntilChanged, filter, map, switchMap } from 'rxjs/operators';
import { v4 as uuidv4 } from 'uuid';

// import * as OpenSeadragon from 'openseadragon';
import { BaseComponentDirective } from '../golden-layout/base-component.directive';
import { ComponentContainer } from 'golden-layout';
import * as OpenSeadragon from 'openseadragon';
import { Button, ControlAnchor } from 'openseadragon';

/*
From:
{
  "@id": "https://www.e-codices.unifr.ch:443/loris/bge/bge-gr0044/bge-gr0044_e001.jp2/full/full/0/default.jpg",
  "@type": "dctypes:Image",
  "format": "image/jpeg",
  "height": 7304,
  "width": 5472,
  "service": {
    "@context": "http://iiif.io/api/image/2/context.json",
    "@id": "https://www.e-codices.unifr.ch/loris/bge/bge-gr0044/bge-gr0044_e001.jp2",
    "profile": "http://iiif.io/api/image/2/level2.json"
  }
}

To:
{
  '@context': 'http://iiif.io/api/image/2/context.json',
  '@id': 'https://www.e-codices.unifr.ch/loris/bge/bge-gr0044/bge-gr0044_e001.jp2',
  'profile': ['http://iiif.io/api/image/2/level2.json'],
  'protocol': 'http://iiif.io/api/image',
  'height': 7304,
  'width': 5472,
}
*/
function manifestResourcetoTileSource(manifestResource: { service: { [x: string]: any; }; height: any; width: any; }) { // TODO: check if there is a manifest type already defined
  return {
    '@context': manifestResource.service['@context'],
    '@id': manifestResource.service['@id'],
    profile: [manifestResource.service['@profile']],
    protocol: 'http://iiif.io/api/image',
    height: manifestResource.height,
    width: manifestResource.width,
  };
}

@Component({
  selector: 'app-osd',
  templateUrl: './osd.component.html',
  styleUrls: ['./osd.component.scss'],
})
export class OsdComponent extends BaseComponentDirective implements AfterViewInit, OnDestroy {

  @ViewChild('osd', { read: ElementRef, static: true })
  div!: ElementRef;

  private _options!: OpenSeadragon.Options;
  @Input() set options(v) { // TODO: add interface to better type this object
    if (v !== this._options) {
      this._options = v;
      this.optionsChange.next(this._options);
    }
  }
  get options() { return this._options; }
  optionsChange = new BehaviorSubject({});

  private _manifestURL!: string;
  @Input() set manifestURL(v: string) {
    if (v !== this._manifestURL) {
      this._manifestURL = v;
      this.manifestURLChange.next(this._manifestURL);
    }
  }
  get manifestURL() { return this._manifestURL; }
  manifestURLChange = new BehaviorSubject<string | undefined>(undefined);

  // tslint:disable-next-line: variable-name
  private _page: number = 0; // TODO: check if 0 is a good initial value
  @Input() set page(v: number) {
    if (v !== this._page) {
      this._page = v;
      this.pageChange.next(this._page);
    }
  }
  get page() { return this._page; }
  @Output() pageChange = new EventEmitter<number>();

  @Input() text: string = '';

  tileSources: Observable<Array<OpenSeadragon.TileSource>> = this.manifestURLChange
    .pipe(
      filter((url) => !!url),
      distinctUntilChanged(),
      switchMap((url) => this.http.get<{ sequences: Partial<Array<{ canvases: any[] }>> }>(url!)), // TODO: check  if there is an already defined type for manifest
      map((manifest) => manifest // get the resource fields in the manifest json structure
        .sequences.map((seq) => seq!.canvases.map((canv) => canv.images).reduce((x, y) => x.concat(y), [])) // TODO: check if seq can be undefined
        .reduce((x, y) => x.concat(y), []).map((res: { resource: any; }) => res.resource) // TODO: check if there is an already defined type for res
        .map(manifestResourcetoTileSource),
      ),
    );

  viewer!: OpenSeadragon.Viewer;
  viewerId: string;

  private subscriptions: Subscription[] = [];

  constructor(
    private http: HttpClient,
    private ngZone: NgZone,
    @Inject(BaseComponentDirective.GoldenLayoutContainerInjectionToken) private container: ComponentContainer,
    elRef: ElementRef,
  ) {
    super(elRef.nativeElement);
    this.viewerId = uuidv4();
    this.subscriptions.push(
      this.pageChange.pipe(
        distinctUntilChanged(),
      ).subscribe((x) => {
        if (!!this.viewer) {
          this.viewer.goToPage(x - 1);
        }
      }));
  }

  ngAfterViewInit() {
    const commonOptions = {
      visibilityRatio: 0.1,
      minZoomLevel: 0.5,
      defaultZoomLevel: 1,
      sequenceMode: true,
      showRotationControl: true,
      prefixUrl: 'assets/osd/images/',
      element: this.div.nativeElement,
      navigatorBackground: '#606060',
      showNavigator: false,
      gestureSettingsMouse: {
        clickToZoom: false,
        dblClickToZoom: true,
      },
    };

    ;
    this.subscriptions.push(combineLatest([this.optionsChange, this.tileSources])
      .subscribe(([options, tileSources]) => {
        this.viewer = this.ngZone.runOutsideAngular(() =>
          OpenSeadragon({
            ...commonOptions,
            showFullPageControl: false,
            ...(!!tileSources ? { tileSources } : options),
          })
        );


        // this.viewer.addHandler('open', () => {
        //   let printButton = new Button({
        //     tooltip: 'Print',
        //     srcRest: `/assets/osd/colors_invert_icon.png`,
        //     srcGroup: `/assets/osd/colors_invert_icon.png`,
        //     srcHover: `/assets/osd/colors_invert_icon.png`,
        //     srcDown: `/assets/osd/colors_invert_icon.png`,
        //     onClick: () => alert('hello'),
        //   });

        //   this.viewer.addControl(printButton.element, { anchor: ControlAnchor.TOP_LEFT });
        // });


        this.viewer.element.addEventListener('scroll', (e) => console.log('asdasasd', e));
        this.viewer.addHandler('page', ({ page }) => this.pageChange.next(page! + 1)); // TODO: check undefined
        ['focus', 'click', 'enter'].forEach((e) =>
          this.viewer.addHandler(e as OpenSeadragon.ViewerEventName, (evt) => console.log(e, evt))
        );
      }));
  }

  ngOnDestroy(): void {
    this.subscriptions.forEach((s) => s.unsubscribe());
  }
}