import { Injectable } from '@angular/core';
import { CoherenceCheck, Language, Warning } from 'cophi-lang';
import { GsLiterary } from 'gs-lit';
import { GsLiteraryApp } from 'gs-lit-app';

import { Subject } from 'rxjs';

function toLanguageDefinition(l: Language, id: string): LanguageDefinition { // TODO: use only Language
  return {
    id, // TODO: change cophi-lang definition
    syntaxHighlighter: l.highlighter,
    completionProvider: (model, position) => { // TODO: replace with actual completion provider
      var word = model.getWordUntilPosition(position);
      var range = {
        startLineNumber: position.lineNumber,
        endLineNumber: position.lineNumber,
        startColumn: word.startColumn,
        endColumn: word.endColumn
      };
      return {
        suggestions: [{
          label: 'Sudhaus',
          kind: monaco.languages.CompletionItemKind.Function,
          documentation: 'The editor Sudhaus',
          insertText: 'Sudhaus',
          range: range
        },
        {
          label: 'Cirillo',
          kind: monaco.languages.CompletionItemKind.Text,
          documentation: 'The editor Cirillo',
          insertText: 'Cirillo',
          range: range
        },
        ],
      }
    },
    errorProvider: (model) => {
      return l.errors(model.getValue())
        .then((errs) => errs.map(({ message, range }) => (
          {
            code: model.uri.toString(),
            startLineNumber: range.start.line,
            startColumn: range.start.column,
            endLineNumber: range.end.line,
            endColumn: range.end.column,
            message: message,
            severity: monaco.MarkerSeverity.Error
          } as monaco.editor.IMarkerData
        ),
        ));
    }
  }
}

@Injectable({
  providedIn: 'root'
})
export class LanguagesService {

  cks = Object.values(coherenceChecks)

  checkers(langs: string[]): CoherenceCheck[] {
    return this.cks.filter((c) => c.languages.every((l) => langs.includes(l)));
  }

  registerLanguage(lang: LanguageDefinition, editorModelChange: Subject<ModelChangeEvent>): monaco.IDisposable[] {
    if (monaco.languages.getLanguages().some((x) => x.id === lang.id)) { return []; }

    monaco.languages.register({ id: lang.id });
    return [ // TODO check if we need to dispose this in case of multiple editor with the same language

      // TODO: test custom tokenizer using ANTLR
      // monaco.languages.setTokensProvider(lang.id, {
      //   getInitialState() {
      //     return new State('');
      //   },
      //   tokenize(line, startState) {
      //     const { tokens, state } = tokenizer.getLineTokens(line, startState.state);
      //     let offset = 0;

      //     const monacoTokens = tokens.map((token) => {
      //       const tok = {
      //         startIndex: offset,
      //         scopes: token.type,
      //       };

      //       offset += token.value.length;

      //       return tok;
      //     });

      //     return {
      //       endState: new State(state),
      //       tokens: monacoTokens,
      //     };
      //   }
      // }),

      monaco.languages.setMonarchTokensProvider(lang.id, lang.syntaxHighlighter),
      monaco.languages.registerCompletionItemProvider(lang.id, { provideCompletionItems: lang.completionProvider }),
      monaco.languages.onLanguage(lang.id, () => {
        monaco.editor.onDidCreateModel((model) => {
          if (model.getLanguageId() === lang.id) {
            model.onDidChangeContent(() => editorModelChange.next({ model, lang }));
          }
        });
      }),
    ];



  }
}

export interface ModelChangeEvent {
  model: monaco.editor.ITextModel;
  lang: LanguageDefinition;
}

export interface LanguageDefinition {
  id: string;
  syntaxHighlighter: monaco.languages.IMonarchLanguage | monaco.Thenable<monaco.languages.IMonarchLanguage>;
  completionProvider: (model: monaco.editor.ITextModel, position: monaco.Position, context: monaco.languages.CompletionContext, token: monaco.CancellationToken) => monaco.languages.ProviderResult<monaco.languages.CompletionList>;
  errorProvider: (model: monaco.editor.ITextModel) => Promise<monaco.editor.IMarkerData[]>;
}

