export const coherenceChecks: Record<string, CoherenceCheck> = {
  'equal-number-of-lines': {
    id: 'equal-number-of-lines',
    warnings: (code: string) => {//Promise < Warning[] >,
      const codeObj = JSON.parse(code) as { code: string, lang: string }[];

      const same = codeObj
        .map((x) => x.code.split("\n").length)
        .every((val: number, i: number, arr: number[]) => val === arr[0])
        ;

      return new Promise<Warning[]>((resolve) => {
        if (same) {
          resolve([]);
          return;
        }
        resolve([
          {
            message: 'Diplomatic and literary transcription must have the same number of lines',
            range: {
              start: { line: 1, column: 1 },
              end: { line: Infinity, column: 1 },
            }
          },
        ]
        )
      })
    },
    languages: ['gs-lit', 'gs-diplomatic'],
  },
}