import { Injectable } from '@angular/core';
import { LayoutConfig } from 'golden-layout';
import { MonacoEditorComponent } from '../components/monaco-editor/monaco-editor.component';
import { OsdComponent } from '../components/osd/osd.component';

export class ComponentInfo {
  componentName: string;
  title: string;

  constructor(
    componentName: string,
    title: string,
  ) {
    this.componentName = componentName;
    this.title = title;
  }
}

export class DSLInfo extends ComponentInfo {
  lang: string;
  code: string;

  constructor(dslId: string, content: string, title: string) {
    super(MonacoEditorComponent.name, title);
    this.lang = dslId;
    this.code = content;
  }
}

class OSDInfo extends ComponentInfo {
  manifestURL: string;
  page: number;

  constructor(manifestURL: string, title: string, page?: number) {
    super(OsdComponent.name, title);
    this.manifestURL = manifestURL;
    this.page = page || 0;
  }
}

interface Data {
  id: string;
  label: string;
  parent?: string;
  children: string[];
  component?: ComponentInfo;
}

interface Map<T> { [key: string]: T; }

function mapToArray<T>(m: Map<T>): T[] { return Object.values(m) };

