  rootElements() {
    return mapToArray(this.db)
      .filter((x) => x.parent === undefined);
  }

  path(id: string | null): Array<{ id: string, label: string }> {
    if (!id) { return []; }
    const { label, parent } = this.db[id] || {};
    if (parent === undefined) { return [{ id, label }]; }
    return [...this.path(parent), { id, label }];
  }

  components(id: string | null): { layout?: LayoutConfig, components: ComponentInfo[] } {
    if (!id) { return { components: [] }; }

    const d = this.db[id];
    if (d === undefined) { return { components: [] }; }

    const childrenComponents = d.children.map((c) => this.components(c))
      .map(({ components }) => components)
      .reduce((x, y) => [...x, ...y], [])
      ;

    const component = d.component !== undefined ? [d.component] : [];
    return { components: [...component, ...childrenComponents] };
  }

  item(id: string) {
    return this.db[id];
  }

