import { JsonpClientBackend } from '@angular/common/http';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CoherenceCheck } from 'cophi-lang';
import { combineLatest, forkJoin, NEVER, Observable, of, Subject } from 'rxjs';
import { map, switchMap, takeUntil, tap, withLatestFrom } from 'rxjs/operators';
import { GoldenLayoutHostComponent } from 'src/app/components/golden-layout/golden-layout-host/golden-layout-host.component';
import { MonacoEditorComponent } from 'src/app/components/monaco-editor/monaco-editor.component';
import { OsdComponent } from 'src/app/components/osd/osd.component';
import { TreeViewItem } from 'src/app/components/tree-view/tree-view.component';
import { LanguagesService } from 'src/app/services/languages.service';
import { DSLInfo, MockDataService } from 'src/app/services/mock-data.service';
import { environment } from 'src/environments/environment';

function checkerCodes(cs: CoherenceCheck, cmps: MonacoEditorComponent[]) {
  const tt = checkerTuples(cs.languages, cmps.filter((x) => cs.languages.includes(x.lang)));
  const w = tt.map((components) => {
    const code = JSON.stringify(components.map(({ lang, code }) => ({ lang, code })));
    return {
      code,
      components,
    }
  });

  return w;

}

// TODO: test function
function checkerTuples(langs: string[], cmps: MonacoEditorComponent[]): MonacoEditorComponent[][] { // Assuming all languages are different
  if (langs.length === 0) { return []; }

  const [l, ...tail] = langs;
  const choice = cmps.find((x) => x.lang === l);

  if (choice === undefined) { return []; }

  const rest = cmps.filter((x) => x !== choice && tail.includes(x.lang));
  if (rest.length === 0) {
    return [[choice]];
  }
  const x = checkerTuples(tail, rest);
  const b = x.map((k) => [choice, ...k]);
  return checkerTuples(tail, rest).map((k) => [choice, ...k]);
}


@Component({
  selector: 'app-working-unit',
  templateUrl: './working-unit.component.html',
  styleUrls: ['./working-unit.component.scss']
})
export class WorkingUnitComponent implements OnInit, OnDestroy {

  id!: string | null;
  private destroy = new Subject();
  viewInit = new Subject();

  breadCrumbs = this.route.paramMap.pipe(
    map((m) => m.get('id')),
    map((id) => this.db.path(id)),
    map((p) => p.map(({ id, label }) => ({ link: `/working-unit/${id}`, label }))),
  );

  components = this.route.paramMap.pipe(
    map((m) => m.get('id')),
    map((id) => this.db.components(id)),
    takeUntil(this.destroy),
  )

  componentsCmd = this.viewInit.pipe(
    switchMap(() => this.components),
    map(({ components }) => {
      this.lc.clear();

      const editors = components.map((c) => this.lc.add(c.componentName, { ...c }).instance)
        .filter((c) => c instanceof MonacoEditorComponent)
        .map((c) => (c as MonacoEditorComponent))

      return editors;
    }),
  );

  editors = new Subject<MonacoEditorComponent[]>();

  checkers = this.components.pipe(
    map(({ components }) => {
      const langs = (components as DSLInfo[])
        .filter(({ lang }) => lang !== undefined)
        .map(({ lang }) => lang)
        ;

      return this.langService.checkers(langs);
    }),
  );

  check = new Subject();

  codes = this.componentsCmd.pipe(
    map((cs) => cs.map((c) => c.codeChange)),
  )



  a = this.check.pipe(
    withLatestFrom(combineLatest([this.checkers]))
  )

  checkCmd = this.check.pipe(
    withLatestFrom(combineLatest([this.checkers, this.componentsCmd, this.a])),
    tap(([_, [cs, cmps]]) => cs.forEach((c) =>
      checkerCodes(c, cmps).forEach(({ code, components }) =>
        c.warnings(code)
          .then((warns) => components.forEach((k) => k.warningsInput.next(warns))))
    )),
    takeUntil(this.destroy),
  )

  treeItems = of(this.db.rootElements()).pipe(
    map((x) => x.map(({ id, label, children }) => ({ id, label, children, link: `/working-unit/${id}` } as TreeViewItem))),
  );

  treeProvider: { getItem: (id: string) => Observable<TreeViewItem> } = {
    getItem: (id: string) => {
      const di = this.db.item(id);
      if (di === undefined) {
        return NEVER;
      }
      return of({ label: di.label, children: di.children, link: `/working-unit/${di.id}` } as TreeViewItem);
    }
  }

  lc!: GoldenLayoutHostComponent;

  constructor(
    public route: ActivatedRoute,
    private langService: LanguagesService,
    public db: MockDataService,
  ) {
    this.componentsCmd.subscribe();
    this.checkCmd.subscribe();
    this.a.subscribe();

    if (environment.production) {
      console.log("INPROD!")
    }else {
      console.log("NOT")
    }
  }

  ngOnDestroy(): void {
    this.destroy.next();
  }

  ngOnInit() {
    this.id = this.route.snapshot.paramMap.get('id');
  }

  addOSD() {
    this.lc.addComponent$.next([OsdComponent.name, { manifestURL: 'https://www.e-codices.unifr.ch/metadata/iiif/ubb-M-I-0018/manifest.json' }]);
  }

  fullScreen(container: HTMLElement) {
    if (!document.fullscreenElement) {
      container.requestFullscreen();
    }
  }
}
