import { Component, ElementRef, Inject } from '@angular/core';
import { ComponentContainer } from 'golden-layout';
import { BaseComponentDirective } from '../golden-layout/base-component.directive';

@Component({
  selector: 'app-editor',
  templateUrl: './editor.component.html',
  styleUrls: ['./editor.component.scss']
})
export class EditorComponent extends BaseComponentDirective {

  editorOptions = {
    theme: 'vs-dark',
    language: 'javascript',
    minimap: { enabled: false },
  }; // TODO: remove this example options with a default
  code: string = 'function x() {\nconsole.log("Hello world!");\n}'; // TODO: remove me

  editorHandle: any;

  constructor(
    @Inject(BaseComponentDirective.GoldenLayoutContainerInjectionToken) private container: ComponentContainer,
    elRef: ElementRef,
  ) {
    super(elRef.nativeElement);
  }

  f(e: any) { // TODO: check race conditions on initialisation
    this.editorHandle = e;
  }

}
