import { Component, OnInit, ViewChild } from '@angular/core';
import { NEVER, Observable, of, Subject } from 'rxjs';
import { map } from 'rxjs/operators';
import { GoldenLayoutHostComponent } from 'src/app/components/golden-layout/golden-layout-host/golden-layout-host.component';
import { OsdComponent } from 'src/app/components/osd/osd.component';
import { TreeViewItem } from 'src/app/components/tree-view/tree-view.component';
import { MockDataService } from 'src/app/services/mock-data.service';

@Component({
  selector: 'app-test-page',
  templateUrl: './test-page.component.html',
  styleUrls: ['./test-page.component.scss']
})
export class TestPageComponent {


  treeItems = of(this.mockDB.rootElements()).pipe(
    map((x) => x.map(({ id, label, children }) => ({ id, label, children } as TreeViewItem))),
  );

  treeProvider: { getItem: (id: string) => Observable<TreeViewItem> } = {
    getItem: (id: string) => {
      const di = this.mockDB.item(id);
      if (di === undefined) {
        return NEVER;
      }
      return of({ label: di.label, children: di.children } as TreeViewItem);
    }
  }


  @ViewChild(GoldenLayoutHostComponent, { static: true })
  lc!: GoldenLayoutHostComponent;

  constructor(
    private mockDB: MockDataService,
  ) {
  }


  addOSD() {
    this.lc.addComponent$.next([OsdComponent.name, { title: "OSD" }]);
  }

}
