import { Component, OnInit } from '@angular/core';
import { of, Observable, NEVER } from 'rxjs';
import { map } from 'rxjs/operators';
import { TreeViewItem } from 'src/app/components/tree-view/tree-view.component';
import { MockDataService } from 'src/app/services/mock-data.service';

@Component({
  selector: 'app-working-units',
  templateUrl: './working-units.component.html',
  styleUrls: ['./working-units.component.scss']
})
export class WorkingUnitsComponent implements OnInit {

  units = [
    { label: 'Col64', id: '64' },
    { label: 'Col65', id: '65' },
    { label: 'Col66', id: '66' },
    { label: 'Col67', id: '67' },
    { label: 'Col68', id: '68' },
    { label: 'Col69', id: '69' },
  ];

  /*
      localhost/working-unit/:id

      nella view

      path: (id: string) => []string

      components: (id:string) => []{ componentName: string, state: any, layout: GoldenLayoutConfiguration }

      
  */

      treeItems = of(this.db.rootElements()).pipe(
        map((x) => x.map(({ id, label, children }) => ({ id, label, children, link: `/working-unit/${id}` } as TreeViewItem))),
      );
    
      treeProvider: { getItem: (id: string) => Observable<TreeViewItem> } = {
        getItem: (id: string) => {
          const di = this.db.item(id);
          if (di === undefined) {
            return NEVER;
          }
          return of({ label: di.label, children: di.children, link: `/working-unit/${di.id}` } as TreeViewItem);
        }
      }

  constructor(
    private db: MockDataService,
  ) {
  }

  ngOnInit(): void {
  }

}
