import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginPageComponent } from './pages/login-page/login-page.component';
import { TestPageComponent } from './pages/test-page/test-page.component';
import { WorkingUnitComponent } from './pages/working-unit/working-unit.component';
import { WorkingUnitsComponent } from './pages/working-units/working-units.component';

const routes: Routes = [
  { path: 'login', component: LoginPageComponent },
  { path: 'working-units', component: WorkingUnitsComponent },
  { path: 'working-unit/:id', component: WorkingUnitComponent },
  { path: 'test', component: TestPageComponent },
  { path: '**', component: TestPageComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
